import { CVData } from './CVParser';

export interface JobRecommendation {
  title: string;
  reason: string;
  matchScore: number;
}

export interface CVInsight {
  category: string;
  title: string;
  description: string;
  suggestion: string;
  priority: 'high' | 'medium' | 'low';
}

export interface EnhancementSuggestion {
  originalText: string;
  suggestion: string;
  reason: string;
  impact: string;
}

export class AIAnalyzer {
  static generateJobRecommendations(cvData: CVData, language: 'fr' | 'en'): JobRecommendation[] {
    const recommendations: JobRecommendation[] = [];
    
    // Quebec job market analysis based on skills and experience
    const quebecJobMarket = {
      'Marketing Manager': {
        keywords: ['marketing', 'digital marketing', 'social media', 'content creation', 'seo'],
        reason: language === 'fr' 
          ? 'Vos comptences en marketing digital et gestion de contenu correspondent parfaitement aux besoins du march qubcois.'
          : 'Your digital marketing and content management skills perfectly match Quebec market needs.'
      },
      'Digital Marketing Specialist': {
        keywords: ['digital marketing', 'social media', 'seo', 'analytics', 'content creation'],
        reason: language === 'fr'
          ? 'Le march qubcois recherche activement des spcialistes en marketing digital avec vos comptences.'
          : 'The Quebec market is actively seeking digital marketing specialists with your skills.'
      },
      'Project Manager': {
        keywords: ['project management', 'leadership', 'planning', 'agile', 'scrum'],
        reason: language === 'fr'
          ? 'Votre exprience en gestion de projet est trs recherche dans les entreprises qubcoises.'
          : 'Your project management experience is highly sought after in Quebec companies.'
      },
      'Business Analyst': {
        keywords: ['analytics', 'data analysis', 'strategy', 'planning', 'excel'],
        reason: language === 'fr'
          ? 'Les entreprises qubcoises ont besoin d\'analystes d\'affaires avec vos comptences analytiques.'
          : 'Quebec companies need business analysts with your analytical skills.'
      },
      'Content Creator': {
        keywords: ['content creation', 'social media', 'writing', 'communication', 'creativity'],
        reason: language === 'fr'
          ? 'Le secteur cratif qubcois recherche des crateurs de contenu bilingues comme vous.'
          : 'The Quebec creative sector is seeking bilingual content creators like you.'
      }
    };

    // Calculate match scores and generate recommendations
    Object.entries(quebecJobMarket).forEach(([title, data]) => {
      const matchingSkills = cvData.skills.filter(skill => 
        data.keywords.some(keyword => skill.toLowerCase().includes(keyword.toLowerCase()))
      );
      
      if (matchingSkills.length > 0) {
        const matchScore = Math.min(100, (matchingSkills.length / data.keywords.length) * 100);
        recommendations.push({
          title,
          reason: data.reason,
          matchScore: Math.round(matchScore)
        });
      }
    });

    // Sort by match score and return top 5
    return recommendations
      .sort((a, b) => b.matchScore - a.matchScore)
      .slice(0, 5);
  }

  static generateCVInsights(cvData: CVData, language: 'fr' | 'en'): CVInsight[] {
    const insights: CVInsight[] = [];

    // Analyze skills
    if (cvData.skills.length < 5) {
      insights.push({
        category: 'skills',
        title: language === 'fr' ? 'Comptences limites' : 'Limited Skills',
        description: language === 'fr' 
          ? 'Votre CV mentionne seulement ' + cvData.skills.length + ' comptences spcifiques.'
          : 'Your CV mentions only ' + cvData.skills.length + ' specific skills.',
        suggestion: language === 'fr'
          ? 'Ajoutez plus de comptences techniques et soft skills pertinentes pour le march qubcois.'
          : 'Add more technical and soft skills relevant to the Quebec market.',
        priority: 'high'
      });
    }

    // Analyze languages
    if (!cvData.languages.includes('french') && !cvData.languages.includes('english')) {
      insights.push({
        category: 'languages',
        title: language === 'fr' ? 'Langues non spcifies' : 'Languages Not Specified',
        description: language === 'fr'
          ? 'Aucune comptence linguistique n\'est mentionne dans votre CV.'
          : 'No language skills are mentioned in your CV.',
        suggestion: language === 'fr'
          ? 'Spcifiez clairement vos comptences en franais et en anglais avec votre niveau de matrise.'
          : 'Clearly specify your French and English skills with your proficiency level.',
        priority: 'high'
      });
    }

    // Analyze experience
    if (cvData.experience.length === 0) {
      insights.push({
        category: 'experience',
        title: language === 'fr' ? 'Exprience professionnelle manquante' : 'Missing Professional Experience',
        description: language === 'fr'
          ? 'Aucune exprience professionnelle n\'a t dtecte dans votre CV.'
          : 'No professional experience was detected in your CV.',
        suggestion: language === 'fr'
          ? 'Ajoutez vos expriences professionnelles avec des descriptions dtailles et des ralisations quantifies.'
          : 'Add your professional experiences with detailed descriptions and quantified achievements.',
        priority: 'high'
      });
    }

    // Analyze education
    if (cvData.education.length === 0) {
      insights.push({
        category: 'education',
        title: language === 'fr' ? 'Formation acadmique manquante' : 'Missing Academic Background',
        description: language === 'fr'
          ? 'Aucune formation acadmique n\'a t dtecte dans votre CV.'
          : 'No academic background was detected in your CV.',
        suggestion: language === 'fr'
          ? 'Incluez votre formation acadmique avec les diplmes obtenus et les institutions frquentes.'
          : 'Include your academic background with degrees obtained and institutions attended.',
        priority: 'medium'
      });
    }

    return insights;
  }

  static generateEnhancementSuggestions(cvData: CVData, selectedText: string, language: 'fr' | 'en'): EnhancementSuggestion {
    const lowerText = selectedText.toLowerCase();
    
    // Common enhancement patterns
    if (lowerText.includes('responsible for') || lowerText.includes('responsable de')) {
      return {
        originalText: selectedText,
        suggestion: language === 'fr'
          ? 'Remplacez "responsable de" par des verbes d\'action plus forts comme "dirig", "gr", "dvelopp"'
          : 'Replace "responsible for" with stronger action verbs like "led", "managed", "developed"',
        reason: language === 'fr'
          ? 'Les verbes d\'action sont plus impactants et montrent mieux vos ralisations.'
          : 'Action verbs are more impactful and better showcase your achievements.',
        impact: language === 'fr' ? 'lev' : 'High'
      };
    }

    if (lowerText.includes('helped') || lowerText.includes('aid')) {
      return {
        originalText: selectedText,
        suggestion: language === 'fr'
          ? 'Quantifiez votre impact avec des chiffres concrets (ex: "augment les ventes de 25%")'
          : 'Quantify your impact with concrete numbers (e.g., "increased sales by 25%")',
        reason: language === 'fr'
          ? 'Les employeurs qubcois recherchent des rsultats mesurables et quantifiables.'
          : 'Quebec employers look for measurable and quantifiable results.',
        impact: language === 'fr' ? 'lev' : 'High'
      };
    }

    if (lowerText.includes('worked on') || lowerText.includes('travaill sur')) {
      return {
        originalText: selectedText,
        suggestion: language === 'fr'
          ? 'Spcifiez votre rle exact et les technologies utilises'
          : 'Specify your exact role and technologies used',
        reason: language === 'fr'
          ? 'Plus de dtails techniques montrent votre expertise spcifique.'
          : 'More technical details show your specific expertise.',
        impact: language === 'fr' ? 'Moyen' : 'Medium'
      };
    }

    // Default suggestion
    return {
      originalText: selectedText,
      suggestion: language === 'fr'
        ? 'Ajoutez des chiffres concrets et des rsultats mesurables pour renforcer cette affirmation'
        : 'Add concrete numbers and measurable results to strengthen this statement',
      reason: language === 'fr'
        ? 'Les employeurs qubcois privilgient les candidats qui peuvent dmontrer leur impact quantifiable.'
        : 'Quebec employers prefer candidates who can demonstrate their quantifiable impact.',
      impact: language === 'fr' ? 'Moyen' : 'Medium'
    };
  }

  static generateQuebecSpecificInsights(cvData: CVData, language: 'fr' | 'en'): CVInsight[] {
    const insights: CVInsight[] = [];

    // Check for Quebec-specific keywords
    const quebecKeywords = ['quebec', 'qubec', 'montreal', 'montral', 'canada', 'canadian'];
    const hasQuebecExperience = quebecKeywords.some(keyword => 
      cvData.text.toLowerCase().includes(keyword)
    );

    if (!hasQuebecExperience) {
      insights.push({
        category: 'quebec',
        title: language === 'fr' ? 'Exprience qubcoise manquante' : 'Missing Quebec Experience',
        description: language === 'fr'
          ? 'Aucune exprience au Qubec ou au Canada n\'est mentionne.'
          : 'No experience in Quebec or Canada is mentioned.',
        suggestion: language === 'fr'
          ? 'Si vous avez de l\'exprience au Qubec, mentionnez-la. Sinon, mettez l\'accent sur votre adaptabilit et votre intrt pour le march qubcois.'
          : 'If you have experience in Quebec, mention it. Otherwise, emphasize your adaptability and interest in the Quebec market.',
        priority: 'medium'
      });
    }

    // Check for French language skills
    if (!cvData.languages.includes('french')) {
      insights.push({
        category: 'french',
        title: language === 'fr' ? 'Comptences en franais' : 'French Language Skills',
        description: language === 'fr'
          ? 'Le franais est essentiel pour la plupart des emplois au Qubec.'
          : 'French is essential for most jobs in Quebec.',
        suggestion: language === 'fr'
          ? 'Indiquez votre niveau de franais et vos efforts pour l\'amliorer.'
          : 'Indicate your French level and efforts to improve it.',
        priority: 'high'
      });
    }

    return insights;
  }
}
